// Responsive Mixin 
@mixin breakpoint($point) {

    // 576 to Upper
    @if $point == xsmall {
        @media (min-width: 250px) { 
            @content ; 
        }
    }
    // 576 to Upper
    @if $point == cxsmall {
        @media (min-width: 200px) and (max-width: 300px) { 
            @content ; 
        }
    }

    // 576 to Upper
    @if $point == small {
        @media (min-width: 576px) { 
            @content ; 
        }
    }

    // 768 to Upper
    @if $point == medium {
        @media (min-width: 768px) { 
            @content ; 
        }
    }

    // 998 to Upper
    @if $point == large {
        @media (min-width: 992px) { 
            @content ; 
        }
    }

    // 1200 to Upper
    @if $point == xlarge {
        @media (min-width: 1200px) { 
            @content ; 
        }
    }

    // 1400 to Upper
    @if $point == xxlarge {
        @media (min-width: 1400px) { 
            @content ; 
        }
    }

    // 1800 to Upper
    @if $point == xxxlarge {
        @media (min-width: 1800px) { 
            @content ; 
        }
    }

}